.cardOuterContainer {
  width: 100%;
  max-width: 430px;
  min-width: 100px;
  height: 270px;
  position: relative;
  top: 90px;
  margin-top: -40px;
  perspective: 1000px;
  opacity: 0.99;

  @media only screen and (max-width: 550px) {
    height: 240px;
    max-width: 375px;
  }

  @media only screen and (max-width: 440px) {
    height: 200px;
    max-width: 315px;
  }

  @media only screen and (max-width: 480px) {
    top: 70px;
  }
}

.cardInnerContainer {
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 1.1s ease-in-out;
  transform-style: preserve-3d;
}

.isFlipped {
  transform: rotateY(180deg);
}

.cardPlaceholder {
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  box-shadow: 3px 3px 15px black;
  border-radius: 10px;
  overflow: hidden;
}

.frontCardGrid {
  grid-template-areas:
    "chip . . type"
    "number number number number"
    "name name name expire";
}

.backCardGrid {
  grid-template-areas:
    "line line line line"
    "cvv cvv cvv cvv"
    ". . . type";
}

.cardRotate {
  transform: rotateY(180deg);
}

.chipPlaceHolder {
  grid-area: chip;
  background-position: 50% 60%;
  background-size: 55%;
  background-repeat: no-repeat;
}

.typeHolder {
  grid-area: type;
  background-position: 20% 70%;
  background-size: 70%;
  background-repeat: no-repeat;
  opacity: 0.9;
}

.number {
  grid-area: number;
  display: flex;
  align-items: center;
}

.numberHolder {
  display: flex;
  flex: 1;
  justify-content: space-around;
  padding: 15px 80px 0 10px;
  color: white;
  font-size: 1.5rem;

  @media only screen and (max-width: 550px) {
    font-size: 1.3rem;
  }

  @media only screen and (max-width: 440px) {
    font-size: 1.1rem;
  }

  @media only screen and (max-width: 440px) {
    font-size: 0.9rem;
  }
}

.quarterHolder {
  display: flex;
}

.numberHolder:focus {
  outline: none;
}

.nameContainer {
  grid-area: name;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 25px;
  overflow: hidden;
}

.name {
  display: flex;
  color: white;
  font-size: 1.05rem;
  margin-top: 4px;
  text-transform: uppercase;

  @media only screen and (max-width: 550px) {
    font-size: 0.9rem;
  }

  @media only screen and (max-width: 440px) {
    font-size: 0.8rem;
  }
}

.expireContainer {
  grid-area: expire;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.expire {
  display: flex;
  color: white;
  font-size: 1.05rem;
  margin-top: 4px;
  text-transform: uppercase;
  letter-spacing: 1px;

  @media only screen and (max-width: 550px) {
    font-size: 0.9rem;
  }

  @media only screen and (max-width: 440px) {
    font-size: 0.8rem;
  }
}

.line {
  grid-area: line;
  align-self: center;
  height: 55%;
  margin-top: 5px;
  background-color: rgba(0, 0, 0, 0.85);
}

.cvvContainer {
  grid-area: cvv;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 20px 10px 20px;
}

.cvvOutput {
  border-radius: 5px;
  height: 55%;
  background-color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 10px;
}

.backTypeHolder {
  grid-area: type;
  background-position: 55% 30%;
  background-size: 60%;
  background-repeat: no-repeat;
  opacity: 0.8;
}

