.valueEnter {
  animation-name: valueEnter;
  animation-duration: 0.3s;
}

@keyframes valueEnter {
  from {
    transform: translateY(-25px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.focusHighlight {
  position: absolute;
  border: 2px solid rgb(202, 202, 202);
  border-radius: 4px;
  transition: all 0.5s ease-in-out;
}

